import React from 'react'
import { Link } from 'react-router-dom'
const NavBar = () => (
    <>
      <nav>
          <ul>
              <li>
                  <Link to='/'>Home</Link>
              </li>
              <li>
                  <Link to='/About'>About</Link>
              </li>
              <li>
                  <Link to='/ArticleList'>Articles List</Link>
              </li>
          </ul>
      </nav>
    </>
)

export default NavBar;