import React from 'react'
import { Link } from 'react-router-dom'

const ArticleList = ({articles}) =>(
    <>
    <h1>Article List </h1>
    {articles.map( (arti, key) => (
        <Link key={key} to={`/ArticlePage/${arti.name}`}>
        <h3>{arti.title}</h3>
        </Link>
    ))}
    </>
);

export default ArticleList